
@import '../node_modules/sn-theme/src/reset.css';
@import url(../node_modules/sn-theme/src/sn-font/snfont.css);
html{font-size:62.5%
}
.radius{border-radius:4px
}
*{box-sizing:border-box
}
body{font-family:'snfont';font-size:14px;background-color:#fefefe
}
a{color:#333
}
a:hover{color:#55B761
}
button.sn-btn{border-radius:4px;padding:.9rem 1.5rem;padding:9px 15px;font-size:1.4rem;font-size:14px;background-color:transparent;border:2px solid #869099;color:#333
}
button.sn-btn:not(.is-disabled):hover{transform:translateY(-2px);transition:transform .2s cubic-bezier(.165, .84, .44, 1),color .2s ease-in-out
}
button.sn-btn:not(.is-disabled):hover{background-color:transparent;border-color:#495057
}
button.sn-btn.is-disabled{border-color:#a4a8ab;color:#a4a8ab
}
button.sn-btn--danger{background-color:#EE3133;border:1px solid #EE3133;color:#fff
}
button.sn-btn--danger:not(.is-disabled):hover{background-color:#e31315;border:1px solid #e31315
}
button.sn-btn--danger.is-disabled{background-color:#f79899;border-color:#f79899;color:#fff
}
button.sn-btn--accessory{background-color:#b26cb2;border:1px solid #b26cb2;color:#fff
}
button.sn-btn--accessory:not(.is-disabled):hover{background-color:#a154a1;border:1px solid #a154a1
}
button.sn-btn--accessory.is-disabled{background-color:#d9b6d9;border-color:#d9b6d9;color:#fff
}
button.sn-btn--warning{background-color:#f7ba2a;border:1px solid #f7ba2a;color:#fff
}
button.sn-btn--warning:not(.is-disabled):hover{background-color:#efab09;border:1px solid #efab09
}
button.sn-btn--warning.is-disabled{background-color:#fbdd95;border-color:#fbdd95;color:#fff
}
button.sn-btn--primary{background-color:#55B761;border:1px solid #55B761;color:#fff
}
button.sn-btn--primary:not(.is-disabled):hover{background-color:#44a04f;border:1px solid #44a04f
}
button.sn-btn--primary.is-disabled{background-color:#aadbb0;border-color:#aadbb0;color:#fff
}
button.sn-btn--tinny{padding:2px 6px;font-size:12px
}
button.sn-btn--small{padding:4px 10px;font-size:1.2rem;font-size:12px
}
button.sn-btn--large{padding:1rem 2rem;padding:10px 20px;font-size:1.6rem;font-size:16px
}
input.sn-input{border-radius:4px;border:2px solid rgba(0,0,0,0.15);height:38px;line-height:38px;padding-left:14px;font-size:14px
}
input.sn-input:focus{outline:none;border:.2rem solid #80BDFF;border:2px solid #80BDFF
}
input.sn-input--tinny{height:30px;line-height:30px;font-size:12px
}
input.sn-input--small{height:34px;line-height:34px;font-size:12px
}
input.sn-input--large{height:42px;line-height:42px;font-size:14px
}
.sn-tag{display:inline-block;line-height:20px;height:22px;padding:0 8px;border-radius:4px;border:1px solid #13ce66;background:#13ce66;-webkit-transition:all .3s cubic-bezier(.215, .61, .355, 1);transition:all .3s cubic-bezier(.215, .61, .355, 1);opacity:1;margin-right:8px;cursor:pointer;white-space:nowrap
}
.sn-tag:hover{background-color:#13ce66;border:1px solid #13ce66;color:#fff
}
.sn-tag:hover:not(.is-disabled):hover{background-color:#10a954;border:1px solid #10a954
}
.sn-tag:hover.is-disabled{background-color:#89e7b3;border-color:#89e7b3;color:#fff
}
.sn-tag .snfont.sn-close{font-size:12px
}
.sn-tag--danger{border:1px solid #EE3133;background:#EE3133
}
.sn-tag--danger:hover{background-color:#EE3133;border:1px solid #EE3133;color:#fff
}
.sn-tag--danger:hover:not(.is-disabled):hover{background-color:#e31315;border:1px solid #e31315
}
.sn-tag--danger:hover.is-disabled{background-color:#f79899;border-color:#f79899;color:#fff
}
.sn-tag--primary{border:1px solid #fefefe;background:#fefefe
}
.sn-tag--primary:hover{background-color:#fefefe;border:1px solid #fefefe;color:#fff
}
.sn-tag--primary:hover:not(.is-disabled):hover{background-color:#eaeaea;border:1px solid #eaeaea
}
.sn-tag--primary:hover.is-disabled{background-color:#fff;border-color:#fff;color:#fff
}
.sn-tag--accessory{border:1px solid #b26cb2;background:#b26cb2
}
.sn-tag--accessory:hover{background-color:#b26cb2;border:1px solid #b26cb2;color:#fff
}
.sn-tag--accessory:hover:not(.is-disabled):hover{background-color:#a154a1;border:1px solid #a154a1
}
.sn-tag--accessory:hover.is-disabled{background-color:#d9b6d9;border-color:#d9b6d9;color:#fff
}
.sn-tag--warning{border:1px solid #f7ba2a;background:#f7ba2a
}
.sn-tag--warning:hover{background-color:#f7ba2a;border:1px solid #f7ba2a;color:#fff
}
.sn-tag--warning:hover:not(.is-disabled):hover{background-color:#efab09;border:1px solid #efab09
}
.sn-tag--warning:hover.is-disabled{background-color:#fbdd95;border-color:#fbdd95;color:#fff
}
.sn-tag--info{border:1px solid #80BDFF;background:#80BDFF
}
.sn-tag--info:hover{background-color:#80BDFF;border:1px solid #80BDFF;color:#fff
}
.sn-tag--info:hover:not(.is-disabled):hover{background-color:#57a8ff;border:1px solid #57a8ff
}
.sn-tag--info:hover.is-disabled{background-color:#c0deff;border-color:#c0deff;color:#fff
}
body{background:#987077
}
.demo{text-align:center;margin-top:100px
}


